.gallery {
  flex-wrap: wrap;
  justify-content: flex-start;
  display: flex;
}

.photo-card {
  width: 200px;
  height: 100%;
  margin: 10px;
}

.photo-card img {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.info {
  text-align: center;
}

.info-item {
  margin: 5px 0;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  margin: 0 auto;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
  display: none;
}

.load-more:hover {
  background-color: #0056b3;
}

.search-form {
  text-align: center;
  background-color: rgba(19, 21, 174, .706);
  padding: 30px;
}

